.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo, .header__logo a {
  align-items: center;
  display: flex;
}

.header__logo a img {
  height: 40px;
}

.nav {
  align-items: center;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list-item {
  margin-right: 20px;
}

.nav__list-item:last-child {
  margin-right: 0;
}

.nav__list-item:first-child {
  margin-left: 0;
}

.nav__list-item a {
  text-transform: uppercase;
  text-align: center;
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: block;
}

.nav__list-item a:hover {
  color: #00acdc;
}

.nav__list-item a.is-active {
  color: #00acdc;
  position: relative;
}

.nav__list-item a.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.card {
  box-sizing: border-box;
  text-align: center;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  font-family: Roboto, Helvetica, sans-serif;
  transition: transform .3s;
  display: flex;
}

.card__title {
  box-sizing: border-box;
  color: #060b35;
  text-align: left;
  margin: 0 16px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover {
  transition: transform .3s;
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card__image {
  box-sizing: border-box;
  width: 100%;
  height: auto;
  padding: 33px 19px 40px;
  display: block;
}

.card__code {
  box-sizing: border-box;
  color: #616070;
  align-content: start;
  margin: 4px 16px 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price {
  box-sizing: border-box;
  justify-content: space-between;
  margin: 24px 16px 0;
  display: flex;
}

.card__price-text {
  box-sizing: border-box;
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-num {
  box-sizing: border-box;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 17px;
}

.card__button {
  box-sizing: border-box;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  margin: 16px;
  padding: 11px 16px;
  text-decoration: none;
  transition: background-color .3s;
}

.card__button-text {
  color: #fff;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  box-sizing: border-box;
  background-color: #fff;
}

.card__button:hover .card__button-text {
  color: #00acdc;
}

.card__stars {
  box-sizing: border-box;
  flex-direction: row;
  justify-content: space-between;
  margin: 16px 16px 0;
  display: flex;
}

.card .reviews {
  box-sizing: border-box;
  align-self: flex-end;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.catalog {
  justify-content: center;
  gap: 48px 46px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

@media (width >= 768px) and (width <= 1023px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 488px) and (width <= 767px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width <= 487px) {
  .catalog {
    grid-template-columns: 1fr;
  }
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, Helvetica, sans-serif;
}
