.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: $header-padding;
  background-color: #fff;

  &__logo {
    display: flex;
    align-items: center;

    a {
      display: flex;
      align-items: center;

      img {
        height: $logo-height;
      }
    }
  }
}
